import React, { Fragment } from "react";

export class Stopper extends React.Component {
  constructor(props) {
    super(props);
    this.rotate = "";
  }
  render() {
    // this.rotate = this.props.animated ? "translate(6 -15) rotate(-90)" : "";
    let x = (
      (6 / 1366) *
      Math.max(window.innerWidth, window.innerHeight)
    ).toFixed(2);
    let y = (
      (-15 / 1366) *
      Math.max(window.innerWidth, window.innerHeight)
    ).toFixed(2);
    this.rotate = this.props.animated ? `translate(${x} ${y}) rotate(-90)` : "";
    return (
      <Fragment>
        <svg
          className="stopper"
          version="1.1"
          id="Layer_1"
          xmlns="http://www.w3.org/2000/svg"
          x="0px"
          y="0px"
          width="512px"
          height="512px"
          viewBox="0 0 600 600"
          enableBackground="new 0 0 512 512"
          transform={this.rotate}
        >
          <g>
            <path
              d="M256,32c-88.004,0-160,70.557-160,156.801C96,306.4,256,480,256,480s160-173.6,160-291.199C416,102.557,344.004,32,256,32z
          M256,244.801c-31.996,0-57.144-24.645-57.144-56c0-31.357,25.147-56,57.144-56s57.144,24.643,57.144,56
         C313.144,220.156,287.996,244.801,256,244.801z"
            />
          </g>
        </svg>
      </Fragment>
    );
  }
}

Stopper.defaultProps = {
  animated: false,
};
